body{
  background-color: purple;
  font-size: 20px;
}
.App{
  background-color: #3f3160;
  min-height:550px;
  width: 400px;
  margin: 20px auto;
}
.app-title{
  color: yellow;
  text-align: center;
  font-size: 1cm;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.input-text{
  background-color:#99AAAB;
  border:0; 
  color: Black;
  width: 150px;
  height: 30px;
  margin: 20px;
  padding: 0 10px;
  font-size: 18px;
  border-radius: 10px;
}
.input-text:focus{
  outline: none;
}
.input-text::placeholder{
  color:black;
  opacity: 1;
}
.btn:focus{
  outline: none;
}
.btn{
  text-align: center;
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 15px;
  right: 2mm;
}
.p{
  padding: 10px;
  right: 2mm;
}
.list{
  background-color: yellow;
  opacity: 0.9;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.logo{
  
  display: block;
  margin-left: auto;
  margin-right: auto;
  animation: spin infinite 20s linear;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}